<template>
  <div class="model" ref="model"></div>
</template>

<script>
/* eslint-disable no-unused-vars */
import * as THREE from "../../node_modules/three/build/three.module.js";
import { OBJLoader } from "../../node_modules/three/examples/jsm/loaders/OBJLoader.js";

// let starImg = require("../assets/star.png");
let starImg = require("../assets/star.png");

export default {
  name: "ThreeJS",
  watch: {},
  props: {
    width: {
      type: Number,
      default: window.innerWidth
    },
    height: {
      type: Number,
      default: window.innerHeight
    },
    zoom: {
      type: Number,
      default: 18
    }
  },
  data() {
    let scene; let camera; let renderer; let mixer; let clock; let texture; let particlesGeometry; let particlesMaterial; let particles;
    return {
      scene,
      camera,
      renderer,
      mixer,
      clock,
      texture,
      particlesGeometry,
      particlesMaterial,
      particles,
      xMove:.0001,
      yMove:.0001,
    };
  },
  methods: {
    init() {
      this.scene = new THREE.Scene();

      this.camera = new THREE.PerspectiveCamera( 1000, this.width / this.height, 0.1, 1000);
      this.camera.position.set(0,0,this.zoom);

      this.clock = new THREE.Clock();

      this.renderer = new THREE.WebGLRenderer({ alpha: false, antialias: true});
      this.renderer.setClearColor (0x000000, 1);
      this.renderer.setPixelRatio( window.devicePixelRatio ); // prevents pixelation
      this.renderer.setSize(this.width, this.height);
      this.renderer.outputEncoding = THREE.sRGBEncoding;
      this.renderer.toneMapping = THREE.ACESFilmicToneMapping;
      this.renderer.toneMappingExposure = 1;
      this.renderer.shadowMap.enabled = true;

      this.$refs.model.appendChild(this.renderer.domElement);

    },

    stars() {
      
      const vertices = [];
      for ( let i = 0; i < 6000; i ++ ) {
        const x = THREE.MathUtils.randFloatSpread( 2000 );
        const y = THREE.MathUtils.randFloatSpread( 2000 );
        const z = THREE.MathUtils.randFloatSpread( 2000 );
        vertices.push( x, y, z );
      }

      const textureLoader = new THREE.TextureLoader();
      textureLoader.setCrossOrigin('anonymous');
      let texture = textureLoader.load( starImg );

      texture.wrapS = THREE.RepeatWrapping;
      texture.wrapT = THREE.RepeatWrapping;
      texture.repeat.set( 1,1 );

      const geometry = new THREE.BufferGeometry();
      geometry.setAttribute( 'position', new THREE.Float32BufferAttribute( vertices, 3 ) );
      let particlesMaterial = new THREE.PointsMaterial({
        size: 5,
        transparent: true,
        depthWrite: false,
        color: 0xB8A230,
        map: texture
      });
      let particles = new THREE.Points(geometry, particlesMaterial);
      this.scene.add(particles);
    },

    animate() {
      requestAnimationFrame(this.animate);
      this.renderer.render(this.scene, this.camera);
      let delta = this.clock.getDelta();
      this.camera.rotation.y -= this.xMove;
      this.camera.rotation.x -= this.yMove;
      // this.camera.position.y = this.camera.position.y - (this.yMove * 500);
      // this.camera.position.x = this.camera.position.x + (this.xMove * 500);

      if (this.mixer) this.mixer.update(delta);
    },

    mousemove(e) {
      // console.log(e.screenX - (window.innerWidth / 2), e.screenY - (window.innerHeight / 2))
      this.xMove = ((e.screenX - (window.innerWidth / 2)) / Math.abs(e.screenX - (window.innerWidth / 2)) || 1) / 10000;
      this.yMove = ((e.screenY - (window.innerHeight / 2)) / Math.abs(e.screenY - (window.innerHeight / 2)) || 1) / 10000;
      // this.camera.position.y = e.screenY / 4;
      // this.camera.rotation.x = e.screenY / 1000;
      // this.camera.rotation.y = e.screenX / 1000;
    }
  },
  emits: ["loaded"],
  mounted() {
    this.init();
    this.stars();
    this.animate();
    window.addEventListener("mousemove", this.mousemove);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
canvas {
margin: auto;

}
.model {
  position: fixed;
  left: 0;
  top: 0;
}
</style>
